import React, { Component } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { FormGroup, InputGroup, Label, Input, InputGroupAddon, InputGroupText, Button } from 'reactstrap'
const JSZip = require("jszip");

const { shareNews,calcAccountId } = require('../js/gegeUtil')
const FileSaver = require('file-saver');

class NewsEditor extends Component {

  constructor(props) {
    super(props);
    this.state = {content: 'Edit your content here. 在此编辑要发布的内容.'}
    this.modules = {
      toolbar: [
        [{ 'header': '1' }, { 'header': '2' }, { 'font': [] }],
        [{ size: [] }], [{ align: [] }, 'direction'],
        ['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block'],
        [{ color: [] }, { background: [] }],
        [{ 'list': 'ordered' }, { 'list': 'bullet' },
        { 'indent': '-1' }, { 'indent': '+1' }],
        ['link', 'image'],
        ['clean']
      ],
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      }
    }
    //
    this.fee = 1;
    this.deadline = 5;
    this.price = 1;
    this.tags = ''
    this.title = ''
    this.secret = ''
    this.location = ''
    this.author = ''
  }

  updateTitle(e) {
    if (e && e.target) {
      this.title = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
    }
  }

  publishNews() {
    //1 save to local file
    //2 start torrent
    var jszip = new JSZip();
    jszip.file('ggvnews.html',this.content);
    jszip.file('title',this.title)
    jszip.file('tags',this.tags)
    jszip.file('price',this.price)
    jszip.file('location',this.location)
    jszip.file('author',this.author)
    jszip.file('time',new Date().toLocaleTimeString())

    jszip.generateAsync({type:'blob'}).then((zip)=>{
      FileSaver.saveAs(zip,"ggvnews.gnews");
    })

    /*
    let news = {};
    shareNews(this.secret,news, (ok) => {
      if (ok) {

      } else {

      }
    })
    */
  }

  updateSecret(e) {
    if (e && e.target) {
      this.secret = e.target.value;
      this.author = calcAccountId(this.secret);
    }
  }

  handleChange(value) {
    this.setState({ content: value })
  }

  render() {
    return (
      <div className="animated fadeIn">
        <FormGroup>
          <Label htmlFor="appendedInputButtons">BTB Account Secret(BTB 账户密码):</Label>
          <div className="controls">
            <InputGroup>
              <Input id="appendedInputButtons" size="16" type="text" onChange={this.updateSecret.bind(this)} placeholder='account secret 输入账户密码發佈资讯' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <InputGroupAddon addonType="prepend">
                <InputGroupText>News Title(资讯标题):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedInputButtons" size="16" type="text" onChange={this.updateTitle.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup>
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Keywords(关键字):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedInputButtons" size="16" type="text" onChange={this.updateTags.bind(this)} />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Price(价格):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder='news price 1 BTB by default, 资讯价格,默认 1 个 BTB币' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Fee(矿工手续费):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='Miner fee 1 BTB by default,矿工手续费,最少1个BTB币' />
              <InputGroupAddon addonType="append">
                <InputGroupText>BTB</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>Deadline(有效期):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='deadline 5minutes by default,交易有效期，默认5分钟' />
              <InputGroupAddon addonType="append">
                <InputGroupText>minutes(分钟)</InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <Label htmlFor="appendedInputButtons">Content(正文):</Label>
          <ReactQuill value={this.state.content}
            modules={this.modules}
            onChange={this.handleChange.bind(this)} />
        </FormGroup>
        <Button type="submit" color="primary" onClick={(e) => this.publishNews()}>Publish(发布内容)</Button>
      </div>
    );
  }
}

export default NewsEditor;



